:root {
  --panel-open-bg: var(--search-results-bg-color);
}

[data-theme="dark"] {
  --panel-open-bg: var(--search-results-bg-color);
}
 
.panel {
  display: flex;
  flex-direction: column;
  
  width: 408px;
  height: 100vh;

  background-color: var(--panel-open-bg);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);

  z-index: 1;
}

.panel:not(.open) {
  display: none;
}

.panel .omnibox-spacer {
  flex: 0 0 64px;
}
